.loader {
  font-size: 48px;
  font-weight: bold;
  letter-spacing: 2px;
  font-family: Arial, Helvetica, sans-serif;
  color: #000;
  animation: smokeOut 1s ease-in-out infinite alternate;
  text-shadow: 0 0 1px rgb(63, 59, 59);
}

.loader:before {
  content: 'Loading data, please wait...';
}

@keyframes smokeOut {
  100% {
    opacity: 0.08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

.breed-select {
  margin-bottom: 30px;
}

.cat-info {
  display: flex;
  gap: 30px;
}

.text-content {
  width: 500px;
}

.temperament {
  font-weight: 700;
}
